<template>
  <div class="swiper-outer-box">
    <swiper
      class="swiper-outer"
      v-swiper:mySwiper="swiperOptions.options"
      @slideChange="swiperSlideChange"
    >
      <template v-for="item in swiperOptions.slideList">
        <swiper-slide :key="item.id">
          <div
            class="swiper-inner"
            :style="bgUrlComputed(item.bgUrl)"
            bg-img-cover
          ></div>
        </swiper-slide>
      </template>
    </swiper>
    <div class="swiper-pagination-outer">
      <template v-for="(item, index) in swiperOptions.slideList">
        <div
          class="swiper-pagination-inner"
          :class="swiperCurrentProgress(index)"
          :key="index"
        ></div>
      </template>
    </div>
  </div>
</template>
<script>
export default {
  name: "page-home-swiper",
  data() {
    return {
      swiperOptions: {
        options: {
          direction: "horizontal",
          slidesPerView: 0,
          initialSlide: 0,
          loop: false,
          observer: true,
          observerParents: true,
          observeSlideChildren: true,
          touchRatio: 0.1,
          resistanceRatio: 0.01,
        },
        progress: 0,
        slideList: [
          {
            id: "slide_1",
            bgUrl: `${process.env.BASE_URL}/imgs/home/home-swiper-1.png`,
          },
          {
            id: "slide_2",
            bgUrl: `${process.env.BASE_URL}/imgs/home/home-swiper-2.png`,
          },
        ],
      },
    };
  },
  computed: {
    bgUrlComputed() {
      return (bgUrl) => {
        return {
          "background-image": `url(${bgUrl})`,
        };
      };
    },
    swiperCurrentProgress() {
      return (index) => {
        return index == Math.abs(this.swiperOptions.progress) ? "checked" : "";
      };
    },
  },
  methods: {
    swiperSlideChange({ activeIndex }) {
      this.swiperOptions.progress = activeIndex;
    },
  },
};
</script>
<style lang="scss" scoped>
.swiper-outer-box {
  position: relative;

  .swiper-outer {
    width: 100vw;
    height: calc(100vw * 1218 / 4096);

    .swiper-inner {
      width: 100%;
      height: 100%;
    }
  }

  .swiper-pagination-outer {
    width: 100%;
    height: 4vw;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 1;

    display: flex;
    justify-content: center;
    align-items: center;

    .swiper-pagination-inner {
      width: 1vw;
      height: 0.2vw;
      border-radius: 0.1vw;
      background: rgba(255, 255, 255, 0.4);
      margin: 0 0.1vw;
      transition: background 0.3s;

      &.checked {
        background: rgba(255, 255, 255, 1);
      }
    }
  }
}
</style>
