<template>
  <div class="home-outer">
    <home-swiper></home-swiper>
    <div
      class="img-tips-block-1"
      :style="bgUrlComputed(imgs.tips1)"
      bg-img-contain
    ></div>
    <div
      class="img-tips-block-2"
      :style="bgUrlComputed(imgs.tips2)"
      bg-img-contain
    ></div>
    <div class="controller-block-outer">
      <template v-for="item in controllerData">
        <div
          :key="item.id"
          class="controller-block"
          :style="bgUrlComputed(imgs.boxshadow_bg)"
          bg-img-contain
        >
          <div
            class="controller-icon"
            :style="bgUrlComputed(item.icon)"
            bg-img-contain
          ></div>
          <div
            class="controller-text"
            :style="bgUrlComputed(item.textIcon)"
            bg-img-contain
          ></div>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import HomeSwiper from "@/components/page-home/swiper";
export default {
  name: "home",
  components: { HomeSwiper },
  data() {
    return {
      imgs: {
        tips1: `${process.env.BASE_URL}/imgs/home/home-tips-1.png`,
        boxshadow_bg: `${process.env.BASE_URL}/imgs/home/shadow-bg.png`,
        tips2: `${process.env.BASE_URL}/imgs/home/home-tips-2.png`,
      },
      controllerData: [
        {
          id: "zhengfu_1",
          icon: `${process.env.BASE_URL}/imgs/home/controller/icon-zhengfu.png`,
          textIcon: `${process.env.BASE_URL}/imgs/home/controller/text-zhengfu.png`,
        },
        {
          id: "ai_1",
          icon: `${process.env.BASE_URL}/imgs/home/controller/icon-ai.png`,
          textIcon: `${process.env.BASE_URL}/imgs/home/controller/text-ai.png`,
        },
        {
          id: "dianshang_1",
          icon: `${process.env.BASE_URL}/imgs/home/controller/icon-dianshang.png`,
          textIcon: `${process.env.BASE_URL}/imgs/home/controller/text-dianshang.png`,
        },
        {
          id: "hulianwang_1",
          icon: `${process.env.BASE_URL}/imgs/home/controller/icon-hulianwang.png`,
          textIcon: `${process.env.BASE_URL}/imgs/home/controller/text-hulianwang.png`,
        },
        {
          id: "jinrong_1",
          icon: `${process.env.BASE_URL}/imgs/home/controller/icon-jinrong.png`,
          textIcon: `${process.env.BASE_URL}/imgs/home/controller/text-jinrong.png`,
        },
        {
          id: "pinpaishang_1",
          icon: `${process.env.BASE_URL}/imgs/home/controller/icon-pinpaishang.png`,
          textIcon: `${process.env.BASE_URL}/imgs/home/controller/text-pinpaishang.png`,
        },
        {
          id: "yiliaojiankang_1",
          icon: `${process.env.BASE_URL}/imgs/home/controller/icon-yiliaojiankang.png`,
          textIcon: `${process.env.BASE_URL}/imgs/home/controller/text-yiliaojiankang.png`,
        },
        {
          id: "lvyoujiaotong_1",
          icon: `${process.env.BASE_URL}/imgs/home/controller/icon-lvyoujiaotong.png`,
          textIcon: `${process.env.BASE_URL}/imgs/home/controller/text-lvyoujiaotong.png`,
        },
      ],
    };
  },
  computed: {
    bgUrlComputed() {
      return (bgUrl) => {
        return {
          "background-image": `url(${bgUrl})`,
        };
      };
    },
  },
};
</script>
<style lang="scss" scoped>
.home-outer {
  position: relative;
  width: 100%;

  .img-tips-block-1 {
    margin-top: 6vw;
    width: 100vw;
    height: 8vw;
    max-height: 100px;
  }

  .img-tips-block-2 {
    width: 100vw;
    height: 4vw;
    max-height: 60px;
  }

  .controller-block-outer {
    margin-top: 2vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .controller-block {
      width: calc(100vw * 466 / 1920);
      height: calc(100vw * 522 / 1920);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      transform: scale(0.96);
      transition: transform 0.6s;

      &:hover {
        transform: scale(1);
      }

      .controller-icon {
        width: 33%;
        height: 32%;
      }
      .controller-text {
        margin-top: 5%;
        width: 50%;
        height: 6%;
      }
    }
  }
}
</style>
